.TodoListItem{
  padding:1rem;
  display: flex;
  align-items: center;
  &:nth-child(even){
    background: #eeeeee;
  }

  .checkbox{
    cursor: pointer;
    display: flex;
    flex:1;
    align-items: center;
    svg{
      font-size: 1.5rem;
    }
    .text{
      margin-left: 0.5rem;
      flex:1;
    }
    &.checked{
      svg{
        color:#22b8cf;
      }
      .text{
        color:#adb5bd;
        text-decoration: line-through;
      }
    }
  }

  .remove{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color:#ff6b6b;
    cursor: pointer;
    &:hober{
      color:#ff8787;
    }
  }
}